
* {
    font-size: 13px;
}

a {
    text-decoration: none;
    color: inherit;
}

.my {
    display: flex;
    flex-direction: column;
    min-height: 70vh;
    width: 1200px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    padding: 20px 30px;
    box-sizing: border-box;
}

.myBanner {
    position: relative;
    left: 227px;
    width: 855px;
    height: 100px;
    border-bottom: 2px solid black;
    margin-bottom: 10px;
}
.myBanner > h2 {
    position: absolute;
    bottom: 10px;
    left: 5px;
    font-weight: bolder;
}

/* Paging ul li */
ul.pagination {
    justify-content: center;
    align-items: center;
}
ul.pagination .page-link {
    color: black;
}
/* info */
#info .myAccount {
    margin-top: 10px;
}

#info .myAccount > tr > td {
    width: 100px;
    height: 50px;
}
#info .myAccount > tr > td:nth-of-type(2) {
    width: 300px;
}

/* point */
#point .pointBanner {
    width: 800px;
    height: auto;
    border: 1px solid rgb(128, 128, 128);
    border-radius: 20px;
    padding: 20px;
    box-sizing: border-box;
}
#point .pointBanner > p:nth-of-type(2) > span { font-weight: bold }

#point div > .sort {
    padding: 10px;
    box-sizing: border-box;
    margin-bottom: 10px;
}
#point div > .sort > button {
    width: 60px;
    height: 60px;
    margin-right: 10px;
    border-radius: 90px;
    font-size: 12px;

}

/* order */
#order .card {
    display: flex;
    flex-direction: row; /* 수평 배치 */
    width: 800px;
    height: auto;
    margin-bottom: 10px;
}
#order .card > .my-order-img {
    width: 200px;
    height: 200px;
}

#order .card > .card-body { position: relative }
#order .card > .card-body > button {
    position: absolute;
    bottom: 20px;
    right: 20px;
}

/* pick */
#pick .card {
    display: flex;
    flex-direction: row; /* 수평 배치 */
    width: 700px;
    height: auto;
    margin: 0 auto;
    margin-bottom: 10px;
}
#pick .card > .card-img-top {
    width: 150px;
    height: 150px;
}
#pick .card > .card-body { position: relative;}
#pick .card > .card-body span {
    position: absolute;
    bottom: 20px;
    right: 20px;
    font-size: 15px;
}

/* Qna */
#QnaList .find-myQna {
    width: 100%;
    height: 60px;
    padding-top: 10px;
    margin-bottom: 10px;
}
#QnaList select {
    float: right;
    width: 150px;
    height: 35px;
    padding: 5px 30px 5px 10px;
    border-radius: 4px;
    outline: 0 none;
}
#QnaList .search-bar {
    float: right;
    width: 220px;
    height: 35px;
    border-radius: 5px;
    border: solid 1px rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    opacity: 1;
    margin-right: 10px;
}
#QnaList .myQna-list > table { border-top: 1px solid black }
#QnaList .myQna-list > table td {
    width: auto;
    height: 60px;
    line-height: 60px;
}
#QnaList .answer-complete {
    color: red;
    font-weight: bold;
}
/* footer */
footer {
    flex-shrink: 0;
}